@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600;700&display=swap);
* {
  box-sizing: border-box;
}

#root {
  display:flex;
  justify-content:center;
}

body {
  margin: 0;
  padding:0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Barlow Semi Condensed', sans-serif;
  background: radial-gradient(hsl(214, 47%, 23%) ,hsl(237, 49%, 15%));
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
  height: 100vh;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:space-between;
  overflow:hidden;
  padding: 2em;
  
}
.blur {
    filter:blur(10px);
}



@media screen and (min-width: 576px) {
  body{ font-size: 18px; }
  .App {padding: 2em 4em;}
  
}

@media screen and (min-width: 768px) {
  body{ font-size: 20px; }
  .App {padding: 2em 8em;}

}

@media screen and (min-width: 992px) {
  .picked-opponent, .picked-player { order: 0 !important;}
  .win-player, .win-opponent {margin-bottom:-2.5em !important;}
  .header-container {width:55% !important; }
  footer { justify-content:flex-end !important;}
  footer {padding:0em 2em !important;}
  .App {padding: 2em 5em;}
  .player-before, .opponent-before { font-size:32px !important;}
  .player, .opponent{font-size:32px;}
  .result-show {order:0 !important; margin: 0em 3em !important; font-size: 15px !important; flex-grow:1 !important;}
  .choice {font-size:larger;}
  header {font-size:24px !important;}
  .header-container {width: 100% !important;}
}
.main-header {
    width: 100%;
    display:flex;
    align-items:center;
    justify-content: center;
  }

  
.header-container {
    border: solid hsl(217, 16%, 45%);
    width: 100%;
    border-radius: .75em;
    display:flex;
    align-items: center;
    justify-content:space-between;
    padding: .5em .65em;
}

.header__logo {
    padding-right: 1em;
    width:6em;
}

.header__score-container {
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items:center;
    background-color: white;
    border-radius: .5em;
    padding: .5em 1.5em;
}

.header__score {
    margin: 0;
    color: hsl(229, 25%, 31%);
    font-size: 2em;
    font-weight: 700;
}

small {
    font-size: .5em;
    letter-spacing: .1em;;
    color: hsl(229, 64%, 46%);
}


footer {
    display:flex;
    justify-content: center;
    align-items: flex-start;
    width: 100vw;
    padding: 2em 0em;

}.rules-btn {
    background:transparent;
    border: solid .5px white;
    outline: none;
    padding: .8em 2.5em;
    border-radius: .5em;
    color: white;
    font-size: .75em;
    line-height: 8px;
    font-weight: 700;
    letter-spacing: .1em;
    cursor: pointer;
}

.rules-btn:hover {
    transition:all .2s ease-in;
    background: white;
    color:hsl(237, 49%, 15%);
}




.choice {
    background: url(/rock-paper-scissors/static/media/bg-triangle.c0c30b56.svg) no-repeat;
    background-position: center 3.5em;
    background-size: 15em 14em;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-around;
    grid-gap:1.5em;
    gap:1.5em;
}

.choice__upper {
    display:flex;
    grid-gap:3em;
    gap:3em;
}

.choice-bg {
    pointer-events: none;
    width: 80%;
    height: 80%;
    border-radius: 50%;
    box-shadow: inset 0em .25em 0.1em .1em rgb(202, 202, 202);
    background: white;
    display:flex;
    justify-content:center;
    align-items:center;
}

.rock, .paper, .scissors {
    display:flex;
    justify-content: center;
    align-items:center;
    width: 8.5em;
    height: 8.5em;
    border-radius:50%;
    transition:all .2s ease-out;
}

.paper {
    background: linear-gradient(hsl(230, 89%, 62%),hsl(230, 89%, 65%));
    box-shadow: 0px 8px 0px .5px hsl(230, 54%, 38%);
}

.scissors {
    background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
    box-shadow: 0px 8px 0px .5px hsl(40, 77%, 28%);
}

.rock {
    background: linear-gradient(hsl(349, 71%, 52%) , hsl(349, 70%, 56%));
    box-shadow: 0px 8px 0px .5px hsl(349, 55%, 31%);
}

.scissors:hover, .paper:hover, .rock:hover {
    transform:scale(1.2);
    transition:all .2s ease-out;
}


.result-container {
    display:flex;
    flex-wrap: wrap;
    /* flex-direction:column; */
    align-items: center;
    justify-content:space-around;
    width:100%;
    flex-grow:1;
    align-self:flex-start;
}

.player, .opponent, .player-before, .opponent-before {
   display:flex;
   flex-direction:column;
   justify-content: center;
   align-items: center;
   grid-gap:2em;
   gap:2em;
}

.win {
    display:none;
}

.win-player, .win-opponent {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 20em;
    height:20em;
    border-radius:50%;
    z-index:-1;
    animation:grow .5s;
    display:flex;
    justify-content: center;
    align-items:center;
    margin-bottom: 2.5em;
}

.middle {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 85%;
    height:85%;
    border-radius:50%;
}

.inner {
    background: rgba(97, 97, 97, 0.1);
    position:absolute;
    width: 65%;
    height:65%;
    border-radius:50%;
}

p {
    margin: 0;
}

.picked-opponent, .picked-player {
    color:white;
    font-weight: 800;
    font-size: 1em;
    letter-spacing: .05em;
    order:1;
}

/* .chosen-player {
    position:unset;
}

.chosen-opponent {
    position:unset;

} */

.shadow {
    border-radius: 50%;
    background: rgba(19, 18, 18, 0.425);
    height:8.5em;
    width: 8.5em;
    display:flex;
    justify-content:center;
    align-items:center;

}

span {
    position:relative;
    animation:pulse;
    animation-duration: 1s;
    animation-iteration-count: 3;
    color: white;
    font-size:4em;
}

.result-show {
    padding-top:4em;
    display:flex;
    flex-direction: column;
    justify-content:center;
    z-index:1;
    order:3;
    font-size:.95em;
    margin:0em 5em;
}

.result {
    margin:0em 1em;
}

.result-label{
    margin:0;
    padding-bottom: .25em;
    font-size: 3em;
    font-weight: 800;
    color:white;
}

.reset {
    background:white;
    outline: none;
    border:none;
    padding: 1em 1.5em;
    border-radius: .67em;
    color:hsl(237, 49%, 15%);
    font-size: 1em;
    line-height: 1em;
    font-weight: 500;
    letter-spacing: .125em;
    cursor: pointer;
    width:100%;
}

.reset:hover {
    color:hsl(349, 71%, 52%);
    transition: color .2s ease-in;



}
@keyframes pulse{
    0%    { scale: 1; }
    20%   { scale: 1.2; }
    100%  { scale: 1; }
  }


@keyframes grow {
    0%    { scale: 0; }
    20%   { scale: .5; }
    100%  { scale: 1; }
}


.rules {
    color: hsl(229, 25%, 31%);
    position: absolute;
    top: 0;
    background:white;
    z-index:1;
    display:flex;
    justify-content:space-around;
    align-items: center;
    flex-direction:column;
    padding: 1em 2em;
    height: 100vh;
    width: 100%;
}

.rules__close-button {
    background:none;
    border:none;
    cursor: pointer;
    transition:all .2s ease-in;

}

.rules > img {
    margin-bottom: 1em;
    width:90%;
}

.rules__close-button:hover {
    transition:all .2s ease-in;
    transform:scale(1.4);
}
